@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

/** Media Queries **/
.button--primary {
  background-color: #3586ff;
  color: #FFFFFF;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 0.5rem;
  border: none;
}
.button--primary:hover {
  background-color: rgb(27.5, 118.7252475248, 255);
  cursor: pointer;
}

.button--secondary {
  background-color: transparent;
  color: #3586ff;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  border-radius: 0.5rem;
  border: solid 0.2rem;
}
.button--secondary:hover {
  background-color: hsla(0, 0%, -5%, 0);
  cursor: pointer;
}

.button--ghost {
  background-color: transparent;
  color: #FFFFFF;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  border-radius: 0.5rem;
  border: none;
  transition: ease 0.3s;
}
.button--ghost:hover {
  background-color: grey;
  cursor: pointer;
}

