html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 140rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", serif;
  margin: 2.5rem 0;
  font-weight: 600;
}

h1 {
  font-size: 6rem;
  font-weight: 600;
  line-height: 1.3;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 1.8rem;
}

h4 {
  font-size: 2.6rem;
}

.inicio {
  background-image: linear-gradient(to bottom, #599cff, #a8bfff, #ccd3fe, #e8e8fe, #ffffff);
}

.header {
  display: flex;
  flex-direction: column;
  padding: 4rem 3rem 0 3rem;
}

.arriba {
  display: flex;
  height: 3rem;
  justify-content: space-between;
  align-items: center;
}
.arriba a {
  color: #000000;
  text-decoration: none;
}
.arriba .header__logo {
  display: flex;
  align-items: center;
  height: 100%;
  gap: 1rem;
}
.arriba .header__logo .header__logo-image {
  height: 200%;
}
.arriba .header__abrir,
.arriba .header__nav-cerrar {
  display: none;
}
.arriba .header__nav {
  width: 40%;
}
.arriba .header__nav .header__nav-list {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
}
.arriba .header__nav .header__nav-list .header__nav-link {
  font-weight: 500;
}
.arriba .header__registro {
  transition: ease 0.5s;
}
.arriba .header__registro:hover {
  transform: scale(1.07);
}
@media (max-width: 850px) {
  .arriba .header__abrir,
  .arriba .header__nav-cerrar {
    display: block;
    width: 3rem;
  }
  .arriba .header__registro {
    display: none;
  }
  .arriba .header__nav {
    opacity: 0;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    margin-right: 0;
    padding: 3rem;
    background: #3586ff;
    box-shadow: 0 0 0 100vmax rgba(0, 0, 0, 0.5);
  }
  .arriba .header__nav-cerrar {
    width: 3rem;
    display: flex;
    align-self: self-end;
  }
  .arriba .visible {
    opacity: 1;
    visibility: visible;
  }
  .arriba .header__nav-list {
    flex-direction: column;
    align-items: start;
    justify-content: space-evenly;
  }
  .arriba .header__nav-list .header__nav-link {
    margin-top: 4rem;
    color: #FFFFFF;
  }
}
@media (max-width: 480px) {
  .arriba .header__nav {
    width: 100%;
  }
  .arriba .header__nav .header__nav-list {
    align-items: center;
    margin-top: 7rem;
  }
}

.abajo {
  text-align: center;
  margin: 15rem auto;
  width: 70rem;
}
@media (max-width: 480px) {
  .abajo {
    width: 95%;
  }
  .abajo h1 {
    font-size: 4rem;
  }
}

.benefits {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-template-rows: auto auto repeat(3, 1fr);
  align-items: center;
}
.benefits__title {
  grid-column: 1/3;
  grid-row: 1;
  margin-bottom: 0;
}
.benefits__description {
  grid-column: 1/3;
  grid-row: 2;
  width: 75rem;
  margin-bottom: 5rem;
}
.benefits__item {
  grid-column: 1;
  border-radius: 0.3rem;
  padding: 1rem;
  padding-left: 2rem;
  margin-bottom: 2.5rem;
  cursor: pointer;
}
.benefits__item:nth-child(3) {
  grid-row: 3;
}
.benefits__item:nth-child(4) {
  grid-row: 4;
}
.benefits__item:nth-child(5) {
  grid-row: 5;
}
.benefits__item h3, .benefits__item p {
  margin-bottom: 0;
  margin-top: 0;
}
.benefits #beneItem1,
.benefits #beneItem2,
.benefits #beneItem3 {
  box-shadow: rgba(9, 30, 66, 0.15) 0px 0.5rem 1rem 0px;
}
.benefits img {
  grid-column: 2;
  grid-row: 3/6;
  width: 90%;
  height: auto;
  object-fit: cover;
  justify-self: end;
  align-self: center;
  border-radius: 1rem;
}

.features__items {
  margin: 0 auto;
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(2, 1fr);
  width: fit-content;
}
.features__items .features__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #FFFFFF;
  width: 25rem;
  height: 25rem;
  padding: 3rem;
  text-align: center;
  border-radius: 1rem;
}
.features__items .features__item:nth-child(1) {
  background: rgb(255, 116, 82);
}
.features__items .features__item:nth-child(2) {
  background: #3586ff;
}
.features__items .features__item:nth-child(3) {
  background: rgb(87, 217, 163);
}
.features__items .features__item:nth-child(4) {
  background: rgb(255, 196, 0);
}
.features__items .features__item h3 {
  margin: 0;
  margin-bottom: 1.5rem;
}
.features__items .features__item img {
  width: 50%;
  margin: 0;
}

.testimonials__container {
  width: 70%;
  margin: 0 auto;
  display: flex;
  gap: 1rem;
  align-items: center;
  background: #d7e4f4;
  padding: 2rem;
  margin-bottom: 1.5rem;
  border-radius: 1rem;
  justify-content: space-between;
}
.testimonials__container .testimonials__icon {
  width: 18rem;
  background: #FFFFFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  gap: 1rem;
  border-radius: 1rem;
}
.testimonials__container .testimonials__icon img {
  width: 5rem;
}
.testimonials__container .testimonials__icon p {
  text-align: right;
  width: fit-content;
}

.footer {
  background: #000000;
  display: flex;
  align-items: center;
  height: fit-content;
  justify-content: space-evenly;
  padding: 2rem;
}
.footer .footer__redes {
  display: flex;
  justify-content: space-between;
  width: 20rem;
}
.footer .footer__redes img {
  height: 3.5rem;
}
.footer .footer__title {
  color: #FFFFFF;
}
.footer .footer__title a {
  color: #FFFFFF;
  text-decoration: underline;
}

